@import '../mixins.scss';
@import '../variables.scss';

.backgroundProduct{
  height: 100vh;
  width:100vw;
  &::after{
    position:absolute;
    content:"";
    width:inherit;
    height: inherit;
    left:0;
    top:0;
    background: rgba(0, 0, 0, 0.392);
  }
  .backgroundProductImg{
    height: inherit;
    width:inherit;
    object-fit: cover;
  }
  .productWrapper{
    position:absolute;
    width:90vw;
    height: 85vh;
    top:50%;
    left:50%;
    transform:translate(-50% ,-50%);
    border-radius: 10px;
    box-shadow: 20px 30px 20px rgba(0, 0, 0, 0.403); 
    border-radius: 10px;
    background: rgba(182, 179, 179,0.2);
    @include flex;
    z-index: 1;
     //backdrop-filter: blur(50px);

    .productDetail{
      @include flex;
      height:inherit;
      width:inherit;
      border-radius: 10px;

      .shoesDescription{
        @include flex($flex-direction:column,$align-items: flex-start);
        width: 40vw;
        margin:auto ;

        h3,p,h4{
          margin-bottom:4vh;
          color:#fff;
        }
        h3{
          font-size: 3rem;
          color:$yellow;
        }
      }
    } 
  }
}


// scss of Swiper and Slider (Product image)

  .productImageSlider{
      height: 400px;
      width: 400px;
    .imageSlider{
      height:inherit;
      width:inherit;
    }
    .swiper-button-prev{
      left:0;
      color:$yellow;
    }
    .swiper-button-next{
      right:0;
      color:$yellow;
    }
  }


  
.productImageSliderThumbs{
  width:400px;
  height:60px;
  .swiper-slide{ 
    border:1px solid transparent;
    &-thumb-active{
      border-color:$yellow;
    }
  }

  .productImageSliderThumbsWrapper{
    width:inherit;
    height: inherit;
    
    .similarImg{
      height: inherit;
      width:inherit;
      margin:auto;
      //margin: 7px;
      border-radius: 5px;
      cursor:pointer;
      object-fit: contain;
    }
  }
  .swiper-button-prev{
    left: -40px;
    color:$yellow;
    transform:scale(0.5);
  }
  .swiper-button-next{
    right:-40px;
    color:$yellow;
    transform:scale(0.5)
  }
}
